import React, { useState, useEffect } from 'react';
import logo from '../logo.svg';
import defaultImage from '../Image/sen.jpg';
import Typography from "@material-ui/core/Typography";
import Slider from "@material-ui/lab/Slider";
import './Hooks.css';

function ImageSlider ({ value, max, onChange, children }) {
  return (
    <>
      <Typography id="label">
        {children}
      </Typography>
      <Slider className="slider"
        min={0}
        max={max}
        value={value}
        aria-labelledby="label"
        step={1}
        onChange={onChange}
      />
    </>
   )
}


export default function Hooks () {
  const [contrast, setContrast] = useState('100%');
  const [brightness, setBrightness] = useState('100%');
  const [invert, setInvert] = useState("0%");
  const [saturate, setSaturate] = useState("100%");
  const [sepia, setSepia] = useState("0%");
  const [grayscale, setGrayscale] = useState('0%');
  const [rotation, setRotation] = useState("0deg");
  const [width, setWidth] = useState('0');
  const [height, setHeight] = useState('0');
  const [allPix, setAllPix] = useState('0');
  const [color, setColor] = useState('#1de9b6');
  const [name, setName] = useState('Franek!');
  const [image, setImage] = useState(defaultImage);

  function onBrightness (e, brightness) {    
    setBrightness(brightness);
  }
  function onGrayscale (e, grayscale) {    
    setGrayscale(grayscale);
  }
  function onContrast (e, contrast) {    
    setContrast(contrast);
  }
  function onInvert (e, invert) {    
    setInvert(invert);
  }
  function onSaturate (e, saturate) {    
    setSaturate(saturate);
  }
  function onSepia (e, sepia) {    
    setSepia(sepia);
  }
  function onRotation (e, rotation) {    
    setRotation(rotation);
  }
  function onBoundsElement (e) {
      let guitarBounds = document.querySelector('.imgStyle');
      let bounds = guitarBounds.getBoundingClientRect();
      console.log( bounds.width + ' width' + ' i ' + bounds.height + ' height.');
      setWidth(bounds.width.toFixed(0));
      setHeight(bounds.height.toFixed(0));
      setAllPix(bounds.width.toFixed(0) * bounds.height.toFixed(0));  
  }
  function fileHandler (e) {
    setImage(URL.createObjectURL(e.target.files[0])
    )
  }
  const container = {
    borderRadius: '5%',
    display: 'grid',
    gridTemplateColumns: 'auto auto auto',
    gridTemplateRows: '80px 200px',
    gridGap: '200px',
    padding: '10px',
    height: '600px',
    backgroundColor: 'rgba(28,34,47,.5)'
  }
  const buttonStyle = {
    cursor: 'pointer'
  }
  const valueStyle = {
    width: '232px',
    maxHeight: '1000px',
    height: '300px',
    borderRadius: '5%'
  }
  const parametersStyle = {
    height: '50px',
    width: '200px',
    marginBlockEnd: '0',
    marginBlockStart: '0',
    padding: '1em',
    color: '#1de9b6'
  }
  const imgStyle = {
    transform: `rotate(${rotation}deg)`,
    filter: `sepia(${sepia}) grayscale(${grayscale}) saturate(${saturate}) invert(${invert}) contrast(${contrast}) brightness(${brightness})`,
  }
  const nameStyle = {
    color: color
  }
  const elementChangingStyle = {
    maxWidth: '600px',
    maxHeight: '600px'
  }
  const headerTitle = {
    color: '#1de9b6',
    fontSize: '40px',
    padding: '1em'
  }
  return (
    <div>
        <div style={headerTitle}>
          React Photo-Modifier <br/> with Hooks
        </div>  
        <div>
          <span>
            <img src={logo} className="App-logo" alt="logo" />
          </span>   
        </div> 
      <div style={container}>
        <div className="settings">
          <ImageSlider
              max={100}
              value={brightness}
              onChange={onBrightness}
          >
              Brightness {brightness}
          </ImageSlider>
          <ImageSlider
              max={100}
              value={contrast}
              onChange={onContrast}
          >
              Contrast {contrast}
          </ImageSlider>
          <ImageSlider
              max={100}
              value={grayscale}
              onChange={onGrayscale}
          >
              Grayscale {grayscale}
          </ImageSlider>
          <ImageSlider
              max={100}
              value={saturate}
              onChange={onSaturate}
          >
              Saturate {saturate}
          </ImageSlider>          
          <ImageSlider
              max={100}
              value={sepia}
              onChange={onSepia}
          >
              Sepia {sepia}
          </ImageSlider>
          <ImageSlider
              max={100}
              value={invert}
              onChange={onInvert}
          >
              Invert {invert}
          </ImageSlider>
          <ImageSlider style={buttonStyle}
              max={360}
              value={rotation}
              onChange={onRotation}
          >
              Rotation {rotation}
          </ImageSlider>
          <div className="buttonContainer">
            <div className="buttonOnSettings">
              <button style={buttonStyle} onClick={() => setName('Lucek')}>Change name</button>
            </div>
            <div className="buttonOnSettings">
              <button style={buttonStyle} onClick={() => setColor('red') }>Change color</button>
            </div>
          </div>
        </div>
        <div style={elementChangingStyle}>
          <img style={imgStyle} src={image}  className="imgStyle"  onClick={onBoundsElement} />
          <p className="nameStyle" style={nameStyle} > {name}</p>
          <div className="buttonImage">
            <input type="file" id="image" onChange={fileHandler} />
          </div>
        </div>
        <div style={valueStyle}>
          <p style={parametersStyle}>Width: {width} px</p>
          <p style={parametersStyle}>Height: {height} px</p>
          <p style={parametersStyle}>Size: {allPix} px</p>
          <div className="buttonImage">
            <button onClick={onBoundsElement} className="cursorPointer" type="button"  >Value of Height/Width/Size"</button>
          </div>
        </div>
      </div>
    </div>
  )  
}